import { roundDecimals } from './toolbox.utils.js';

/**
 *
 * @param {Number} percentage
 * @returns {Number}
 */
const roundPercentage = percentage => {
    if (percentage > 100) {
        percentage = 100;
    } else if (percentage < 0) {
        percentage = 0;
    } else {
        percentage = roundDecimals(percentage, 4);
    }

    return percentage;
};

const isIntersectionObserverSupported = 'IntersectionObserver' in window;

const intersectionObserverThreshold = (factor => {
    let threshold = [];
    if (isIntersectionObserverSupported) {
        for (let i = 0; i <= 100 / factor; i++) {
            threshold.push((i * factor) / 100);
        }
    }
    return threshold;
})(2);

/**
 *
 */
export class Viewport {
    constructor() {
        this._window = window;
        this._html = document.documentElement;
        this._body = document.body;

        this._frame1 = this._window;
        this._frame2 = this._html || this._body;
        this._prefix1 = 'inner';
        this._prefix2 = 'offset';
        if (!this._prefix1 + 'Width' in this._frame1) {
            this._prefix1 = 'client';
            this._frame1 = this._frame2;
        }

        this._offsetWidth = 0;
        this._offsetHeight = 0;
        this._width = 0;
        this._height = 0;
        this._scrollTop = 0;
        this._scrollLeft = 0;

        this._lock = false;

        this.calcAll();
    }

    get offsetWidth() {
        return this._offsetWidth;
    }
    get offsetHeight() {
        return this._offsetHeight;
    }
    get width() {
        return this._width;
    }
    get height() {
        return this._height;
    }
    get scrollTop() {
        return this._scrollTop;
    }
    get scrollLeft() {
        return this._scrollLeft;
    }
    get all() {
        return {
            offsetWidth: this.offsetWidth,
            offsetHeight: this.offsetHeight,
            width: this.width,
            height: this.height,
            scrollTop: this.scrollTop,
            scrollLeft: this.scrollLeft
        };
    }

    calcOffsetWidth() {
        this._offsetWidth = this._body[this._prefix2 + 'Width'];
        return this.offsetWidth;
    }
    calcOffsetHeight() {
        this._offsetHeight = this._body[this._prefix2 + 'Height'];
        return this.offsetHeight;
    }
    calcWidth() {
        this._width = this._frame1[this._prefix1 + 'Width'];
        return this.width;
    }
    calcHeight() {
        this._height = this._frame1[this._prefix1 + 'Height'];
        return this.height;
    }
    calcScrollTop() {
        this._scrollTop = this._frame2.scrollTop || 0;
        return this.scrollTop;
    }
    calcScrollLeft() {
        this._scrollLeft = this._frame2.scrollLeft || 0;
        return this.scrollLeft;
    }
    calcAll() {
        this.calcOffsetWidth();
        this.calcOffsetHeight();
        this.calcWidth();
        this.calcHeight();
        this.calcScrollTop();
        this.calcScrollLeft();
        return this.all;
    }

    get lock() {
        return this._lock;
    }

    set lock(bool) {
        this._lock = bool;

        if (this._lock) {
            this._body.style.top = -this.calcScrollTop() + 'px';
            this._body.style.left = -this.calcScrollLeft() + 'px';

            let scrollBarWidth = this.calcOffsetWidth();
            let scrollBarHeight = this.calcOffsetHeight();

            this._html.style.top = '0px';
            this._html.style.left = '0px';
            this._html.style.position = 'fixed';
            this._body.style.position = 'fixed';
            this._html.style.width = '100%';
            this._body.style.width = '100%';

            let offsetWidth = this.calcOffsetWidth();
            let offsetHeight = this.calcOffsetHeight();
            scrollBarWidth = offsetWidth - scrollBarWidth;
            scrollBarHeight = offsetHeight - scrollBarHeight;

            this._body.style.width = offsetWidth - scrollBarWidth + 'px';
            this._body.style.height = offsetHeight - scrollBarHeight + 'px';
        } else {
            let scrollTop = Math.abs(parseFloat(this._body.style.top));
            let scrollLeft = Math.abs(parseFloat(this._body.style.left));

            this._html.style.position = '';
            this._html.style.top = '';
            this._html.style.left = '';
            this._html.style.width = '';
            this._body.style.position = '';
            this._body.style.top = '';
            this._body.style.left = '';
            this._body.style.width = '';

            this._window.scroll({
                top: scrollTop,
                left: scrollLeft,
                behavior: 'instant'
            });
        }
    }
}

/**
 * @param {DOMRect} bounds
 * @returns {Object}
 */
export const getBoundsViewportIntersection = bounds => {
    const viewport = new Viewport();

    if (bounds.right < 0 || bounds.bottom < 0 || bounds.left > viewport.width || bounds.top > viewport.height) {
        return {
            x: 0,
            y: 0,
            ratio: 0
        };
    }

    const offTop = bounds.top < 0;
    const offBottom = bounds.top + bounds.height > viewport.height;
    const offLeft = bounds.left < 0;
    const offRight = bounds.left + bounds.width > viewport.width;

    if (!offTop && !offBottom && !offLeft && !offRight) {
        return {
            x: 100,
            y: 100,
            ratio: 100
        };
    }

    let offY = 0;
    let offX = 0;

    if (offTop) {
        offY = Math.abs(bounds.top);
    }

    if (offBottom) {
        offY = bounds.top + bounds.height - viewport.height;
    }

    if (offLeft) {
        offX = Math.abs(bounds.left);
    }

    if (offRight) {
        offX = bounds.left + bounds.width - viewport.width;
    }

    const percentageX = roundPercentage(((bounds.width - offX) * 100) / (bounds.width || 1));
    const percentageY = roundPercentage(((bounds.height - offY) * 100) / (bounds.height || 1));

    return {
        x: percentageX,
        y: percentageY,
        ratio: Math.min(percentageX, percentageY)
    };
};

/**
 * @param {HTMLElement} element
 * @returns {Object}
 */
export const getElementViewportIntersection = element => {
    if (isIntersectionObserverSupported) {
        if (!('observer' in element)) {
            element.observer = new IntersectionObserver(
                entries =>
                    entries.forEach(entry => {
                        entry.target.DOMRect = entry.boundingClientRect;
                        entry.target.intersection = entry.isIntersecting
                            ? {
                                  x: roundPercentage((entry.intersectionRect.width / entry.target.DOMRect.width) * 100),
                                  y: roundPercentage((entry.intersectionRect.height / entry.target.DOMRect.height) * 100),
                                  ratio: roundPercentage(entry.intersectionRatio * 100)
                              }
                            : {
                                  x: 0,
                                  y: 0,
                                  ratio: 0
                              };
                    }),
                {
                    threshold: intersectionObserverThreshold
                }
            );

            element.observer.observe(element);
        }

        if (!('intersection' in element) /* TODO: or not triggering intersetionobserver cb in a while ... */) {
            element.DOMRect = element.getBoundingClientRect();
            element.intersection = getBoundsViewportIntersection(element.DOMRect);
        }

        return element.intersection;
    }

    element.DOMRect = element.getBoundingClientRect();
    element.intersection = getBoundsViewportIntersection(element.DOMRect);

    return element.intersection;
};

/**
 * @param {NodeList|Array<HTMLElement>} elements
 * @param {('ratio'|'x'|'y')} mode
 * @returns {Array<HTMLElement>}
 */
export const getInViewportElements = (elements, mode = 'ratio', percentage = 0) => {
    percentage = roundPercentage(percentage);
    return [...elements].filter(percentage === 100 ? el => getElementViewportIntersection(el)[mode] >= percentage : el => getElementViewportIntersection(el)[mode] > percentage);
};

/**
 * @param {NodeList|Array<HTMLElement>} elements
 * @param {('ratio'|'x'|'y')} mode
 * @returns {Array<HTMLElement>}
 */
export const getFullyInViewportElements = (elements, mode = 'ratio') => getInViewportElements(elements, mode, 100);

