/**
 *
 * @param {NodeList|Array<HTMLElement>}  elements
 * @param {('x'|'y'|'both')} axis
 * @returns {Array<HTMLElement>}
 */
export const getOverlappingElements = (elements, mode = 'ratio') => {
    elements = getInViewportElements(elements, mode);

    let rects = [];
    [...elements].forEach(element => {
        if (element.intersection[mode] === 100) {
            element.DOMRect = element.getBoundingClientRect();
        }
        rects.push(element.DOMRect);
    });

    let overlappingElements = [];
    [...elements].forEach((element, i) => {
        let rectA = rects[i];

        for (let x in rects) {
            // skips its own rects
            if (parseInt(x) === i) {
                continue;
            }

            const rectB = rects[x];
            const overlappingX = rectA.right > rectB.left && rectA.left < rectB.right;
            const overlappingY = rectA.bottom > rectB.top && rectA.top < rectB.bottom;
            let overlapping = false;

            switch (mode) {
                case 'x':
                    overlapping = overlappingX;
                    break;
                case 'y':
                    overlapping = overlappingY;
                    break;
                case 'ratio':
                    overlapping = overlappingX && overlappingY;
                    break;
            }

            if (overlapping) {
                overlappingElements.push(element);
            }
        }
    });

    return overlappingElements;
};
