let maxAnisotropy = null;
const getMaxAnisotropy = () => {
    if (maxAnisotropy !== null) {
        return maxAnisotropy;
    }

    const canvas = document.createElement('canvas');
    let gl = null;
    try {
        gl = canvas.getContext('webgl') || canvas.getContext('experimental-webgl');
    } catch (e) {}

    if (null !== gl && 'getExtension' in gl) {
        const ext =
            gl.getExtension('EXT_texture_filter_anisotropic') || gl.getExtension('MOZ_EXT_texture_filter_anisotropic') || gl.getExtension('WEBKIT_EXT_texture_filter_anisotropic');

        if (ext && 'MAX_TEXTURE_MAX_ANISOTROPY_EXT' in ext) {
            maxAnisotropy = gl.getParameter(ext.MAX_TEXTURE_MAX_ANISOTROPY_EXT);
        }
    }

    return maxAnisotropy;
};

export const hasModernHardware = () => getMaxAnisotropy() >= 4;

// iOS safari does
let transformBreaksFixedSystem = false;
export const doesTransformBreakFixed = () => {
    if (transformBreaksFixedSystem !== null) {
        return transformBreaksFixedSystem;
    }

    const a = document.createElement('div');
    const b = document.createElement('div');

    a.style.position = 'absolute';
    a.style.top = '0px';
    a.style.left = '-9999px';
    a.style.width = '1px';
    a.style.height = '1px';
    a.style.visibility = 'hidden';
    a.style.transform = 'translateZ(0)';

    b.style.position = 'fixed';
    b.style.top = '0px';
    b.style.left = '0px';
    b.style.width = '100%';
    b.style.height = '100%';

    document.body.append(a);
    a.append(b);

    transformBreaksFixedSystem = b.getBoundingClientRect().top < 0;

    a.remove();

    return transformBreaksFixedSystem;
};

// firefox does
let fixedBreakStickySystem = false;
export const doesFixedBreakSticky = () => {
    if (fixedBreakStickySystem !== null) {
        return fixedBreakStickySystem;
    }

    const a = document.createElement('div');
    const b = document.createElement('div');
    const c = document.createElement('div');

    a.style.position = 'fixed';
    a.style.top = '0';
    a.style.left = '-9999px';
    a.style.width = '1px';
    a.style.height = '1px';

    b.style.position = 'relative';
    b.style.top = '-9800px';
    b.style.left = '0px';
    b.style.width = '100%';
    b.style.height = '9999px';

    c.style.position = 'sticky';
    c.style.position = '-webkit-sticky';
    c.style.top = '0px';
    c.style.left = '0px';
    c.style.width = '100%';
    c.style.height = '100px';

    document.body.append(a);
    a.append(b);
    b.append(c);

    fixedBreakStickySystem = c.getBoundingClientRect().top < 0;

    a.remove();

    return fixedBreakStickySystem;
};

const userAgent = navigator.userAgent.toLowerCase();
const iOSDevices = ['iphone', 'ipad', 'ipod'];

let os = null;
export const getOS = () => {
    if (os !== null) {
        return os;
    }

    if (userAgent.indexOf('win') !== -1) {
        os = 'windows';
        return os;
    }

    if (userAgent.indexOf('mac') !== -1) {
        os = 'mac';
        return os;
    }

    if (userAgent.indexOf('x11') !== -1) {
        os = 'unix';
        return os;
    }

    if (userAgent.indexOf('linux') !== -1) {
        os = 'linux';
        return os;
    }

    if (getBrowser() !== 'edge' && userAgent.indexOf('android') !== -1) {
        os = 'android';
        return os;
    }

    if (iOSDevices.indexOf(getDevice()) !== -1 || userAgent.indexOf('crios') !== -1) {
        os = 'ios';
        return os;
    }

    os = 'unknown';

    return os;
};

let device = null;
export const getDevice = () => {
    if (device !== null) {
        return device;
    }

    const iosDeviceMatch = userAgent.match(new RegExp(iOSDevices.join('|')));

    if (null !== iosDeviceMatch && !('MSStream' in window)) {
        device = iosDeviceMatch[0];
        return device;
    }

    if (/android|webos|blackberry|iemobile|opera mini|mobile|mobile|crios/i.test(userAgent)) {
        device = 'mobile';
        return device;
    }

    os = os || getOS();
    if (os === 'windows' || os === 'mac' || os === 'unix' || os === 'linux') {
        device = 'desktop';
        return device;
    }

    device = 'unknown';

    return device;
};

let browser = null;
export const getBrowser = () => {
    if (browser !== null) {
        return browser;
    }

    if (/edge\/\d/.test(userAgent)) {
        browser = 'edge';
        return browser;
    }

    if (userAgent.indexOf('chrome') !== -1 || userAgent.indexOf('crios') !== -1) {
        browser = 'chrome';
        return browser;
    }

    if (userAgent.indexOf('firefox') !== -1) {
        browser = 'firefox';
        return browser;
    }

    if (getDevice() !== 'android') {
        browser = 'safari';
        return browser;
    }

    if (userAgent.indexOf('msie ') !== -1 || userAgent.indexOf('trident/') !== -1) {
        browser = 'msie';
        return browser;
    }

    browser = 'unknown';
    return browser;
};

const vendorsPrefixes = ['WebKit', 'Moz', 'O', 'Ms', ''];

export const MutationObserver = (() => {
    for (let i = 0; i < vendorsPrefixes.length; i++) {
        if (vendorsPrefixes[i] + 'MutationObserver' in window) {
            return window[vendorsPrefixes[i] + 'MutationObserver'];
        }
    }

    return false;
})();

export const transitionEndEventName = (() => {
    const el = document.createElement('div');

    const transEndEventNames = {
        WebkitTransition: 'webkitTransitionEnd',
        MozTransition: 'transitionend',
        OTransition: 'oTransitionEnd otransitionend',
        msTransition: 'MSTransitionEnd',
        transition: 'transitionend'
    };

    for (let name in transEndEventNames) {
        if (el.style[name] !== undefined) {
            return transEndEventNames[name];
        }
    }

    return false;
})();

export const intersectionObserverSupported = 'IntersectionObserver' in window;
export const pictureElementSupported = 'HTMLPictureElement' in window;
